@import '../../Style/variables.scss';

nav {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    right: 10px;
    top: 72px;
    // translate: 0 -28px;

    .bars-icon {
        display: none;
    }

    .navbar__list {
        display: flex;
        margin: 0;
        padding: 0;
    }

    .navbar__link {
        padding: 0 1em;
        margin: 0 0 1px 1px;

        &:hover {
            border-left: 1px solid $primary-color;
            border-bottom: 1px solid $primary-color;
            margin: 0;
        }

        &.dropdown-elements {
            display: none;
            border: none;
        }

        &.private-title {
            border: none;
        }

        &.private-list:hover {
            border: none;
            margin: 0 0 1px 1px;

            & .dropdown-element {
                &s {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    padding: 0;
                    background-color: white;
                }

                &1 {
                    margin: 0;
                    animation: show 400ms ease-out 50ms both;
                }

                &2 {
                    margin: 0;
                    animation: show 400ms ease-out 200ms both;
                }
            }
        }
    }

    @media screen and (max-width: 426px) {
        top: 20px;
        right: 10px;

        .bars-icon {
            display: block;
            position: absolute;
            right: 10px;
        }

        .navbar__link {
            padding: 0;
        }

        .navbar__list {
            display: none;
            background-color: white;
            border: 2px solid $secondary-color;
            padding: 5px;
        }

        &:hover {
            display: flex;
            flex-direction: column;

            .bars-icon {
                display: none;
            }

            & .navbar__list {
                display: flex;
                flex-direction: column;
            }

            &.private-list {
                margin: 0;
                padding: 0;
            }
        }
    }
}